<template>
    <button 
    @click="onClick()"
    :style="{background: color}" 
    class="btn">{{text}}</button>
</template>

<script>
export default {
    name: 'Button',
    props:{
        text: String,
        color: String,
        isActive: Boolean,
        
    },
    methods:{
        onClick(){
            console.log('Clicked')
        }
    }
}
</script>
